import {
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  Output,
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { combineLatest, of, Subscription } from 'rxjs';
import { catchError } from 'rxjs/operators';

import { CompanyService } from '@company/company.service';

import { IStatus, LETTER_STATUS } from '@shared/models';
import { NavService } from '@shared/nav/nav.service';

import {
  ILetter,
  ILettersSearch,
  IOnSignCounts,
  LETTER_OUTBOX_STATUS,
} from '../../models';
import { LettersService } from '../../services';

const TRANSLATE_PREFIX = 'LETTERS.STATUSES';

@Component({
  selector: 'j-letters-outbox',
  templateUrl: './letters-outbox.component.html',
  styleUrls: ['./letters-outbox.component.scss'],
})
export class LettersOutboxComponent implements OnDestroy {
  @Input() highlighted: string;
  @Output() detailsShown = new EventEmitter<ILetter>();

  statuses: IStatus[] = [
    {
      label: this.mapTranslate('ON_SIGN'),
      value: LETTER_OUTBOX_STATUS.ON_SIGN,
      count: null,
      isSelected: true,
      isDisabled: false,
    },
    {
      label: this.mapTranslate('SENT'),
      value: LETTER_OUTBOX_STATUS.SENT,
      count: 0,
      isSelected: false,
      isDisabled: false,
    },
  ];

  sentParams: Partial<ILettersSearch> = {
    incoming: false,
    statusCode: [LETTER_STATUS.SENT, LETTER_STATUS.READY_TO_SEND],
  };

  outboxStatus = this.statuses[0].value;
  LETTER_OUTBOX_STATUS = LETTER_OUTBOX_STATUS;
  isOnSignHidden: boolean = false;

  subscription = new Subscription();

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private lettersService: LettersService,
    private navService: NavService,
    private companyService: CompanyService
  ) {
    this.subscription.add(
      combineLatest([
        this.route.data,
        this.companyService.getAuthorizedPersons().pipe(
          catchError((err, caught) => {
            return of([]);
          })
        ),
      ]).subscribe((values) => {
        const [routeData, persons] = values;

        this.isOnSignHidden = persons
          ? this.companyService.hasOnlyPrimaryAuthorities(persons) &&
            this.companyService.isSingleSignScheme()
          : false;

        if (
          this.isOnSignHidden &&
          routeData.status === LETTER_OUTBOX_STATUS.ON_SIGN
        ) {
          this.router.navigate(['sent'], {
            relativeTo: this.route.parent,
          });

          return;
        }

        this.onInit(routeData.status);
        this.lettersService.lastLettersListPath$.next(routeData.lastPath);
      })
    );

    const badgesSubscription = this.navService
      .getLetterBadges()
      .subscribe((badges) => {
        const { onSignForMe, onSignForOthers } = badges;
        const hasOnSignForMe = onSignForMe > 0;

        Object.assign(
          this.statuses.find(
            (status) => status.value === LETTER_OUTBOX_STATUS.ON_SIGN
          ),
          {
            count: hasOnSignForMe ? onSignForMe : onSignForOthers,
            classList: hasOnSignForMe ? 'badge-primary' : 'badge-light',
          }
        );
      });

    this.subscription.add(badgesSubscription);
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  onSignableRetrieved(counts: IOnSignCounts) {}

  onSentRetrieved(count: number) {}

  onDetailsShown(letter: ILetter) {
    this.detailsShown.emit(letter);
  }

  onInit(value: LETTER_OUTBOX_STATUS = LETTER_OUTBOX_STATUS.ON_SIGN) {
    this.outboxStatus = value;
    this.statuses = this.statuses.map((status) => {
      return { ...status, isSelected: status.value === value };
    });
  }

  onStatusSelect(value: LETTER_OUTBOX_STATUS) {
    let path = '';

    switch (value) {
      case LETTER_OUTBOX_STATUS.ON_SIGN:
        path = 'on-sign';
        break;
      case LETTER_OUTBOX_STATUS.SENT:
        path = 'sent';
        break;
    }
    this.router.navigate([path], { relativeTo: this.route.parent });
  }

  mapTranslate(key: string) {
    return `${TRANSLATE_PREFIX}.${key}`;
  }
}
