import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { Subscription } from 'rxjs';

import { LettersService } from '../../services';

@Component({
  selector: 'j-letters-create',
  templateUrl: './letters-create.component.html',
  styleUrls: ['./letters-create.component.scss'],
})
export class LettersCreateComponent implements OnInit, OnDestroy {
  title: string;
  subscription = new Subscription();
  lastLetterListPath: string;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private lettersService: LettersService
  ) {
    this.lettersService.lastLettersListPath$.subscribe((path) => {
      this.lastLetterListPath = path;
    });
  }

  ngOnInit() {
    const routeParamsSubscription = this.route.params.subscribe((params) => {
      if (params.id) {
        this.title = 'LETTERS.CREATE.EDIT';
      } else {
        this.title = 'LETTERS.CREATE.TITLE';
      }
    });

    this.subscription.add(routeParamsSubscription);
  }

  ngOnDestroy() {
    this.lettersService.clearFiles();
    this.subscription.unsubscribe();
  }

  onFileDropped(fileList) {
    this.lettersService.onFileDrop(fileList);
  }

  onClose() {
    this.lettersService.clearFiles();
    this.router.navigate(['/letters', this.lastLetterListPath]);
  }
}
