import {
  animate,
  state,
  style,
  transition,
  trigger,
} from '@angular/animations';
import { DOCUMENT } from '@angular/common';
import {
  Component,
  EventEmitter,
  Inject,
  OnDestroy,
  Output,
} from '@angular/core';
import { ILetter } from '@letters/models';

const BODY_OVERFLOW_CLASS = 'overflow-hidden';

@Component({
  selector: 'j-letters-aside',
  templateUrl: './letters-aside.component.html',
  styleUrls: ['./letters-aside.component.scss'],
  animations: [
    trigger('wrapper', [
      state(
        'shown',
        style({
          transform: 'scale(1)',
          visibility: 'visible',
          opacity: '1',
        })
      ),
      state(
        'hidden',
        style({
          transform: 'scale(0.9)',
          visibility: 'hidden',
          opacity: '0',
        })
      ),
      transition('shown<=>hidden', animate('250ms')),
    ]),
    trigger('content', [
      state(
        'shown',
        style({
          right: '0%',
        })
      ),
      state(
        'hidden',
        style({
          right: '-25%',
        })
      ),
      transition('shown<=>hidden', animate('500ms ease-in-out')),
    ]),
  ],
})
export class LettersAsideComponent implements OnDestroy {
  isContentShown = false;
  letter: ILetter;

  isDetailsShowing = false;
  isCreateShowing = false;

  @Output() closed = new EventEmitter();

  constructor(@Inject(DOCUMENT) private document: Document) {}

  ngOnDestroy() {
    this.document.body.classList.remove(BODY_OVERFLOW_CLASS);
  }

  public showDetails(letter: ILetter) {
    this.isDetailsShowing = true;
    this.isCreateShowing = false;

    this.letter = letter;
    this.showContent();
  }

  public showCreate() {
    this.isCreateShowing = true;
    this.isDetailsShowing = false;

    this.showContent();
  }

  public hideAside() {
    this.letter = null;
    this.isCreateShowing = false;
    this.isDetailsShowing = false;

    this.document.body.classList.remove(BODY_OVERFLOW_CLASS);
    this.isContentShown = false;

    this.closed.emit();
  }

  private showContent() {
    this.document.body.classList.add(BODY_OVERFLOW_CLASS);
    this.isContentShown = true;
  }
}
