import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LETTERS_CATEGORY, LETTER_OUTBOX_STATUS } from './models';
import { LettersCreateComponent, LettersListComponent } from './containers';
import { LettersComponent } from './letters.component';

const routes: Routes = [
  {
    path: '',
    component: LettersComponent,
    data: {
      title: 'CONTRACTS.TITLE',
    },
    children: [
      {
        path: 'outbox',
        children: [
          {
            path: 'on-sign',
            component: LettersListComponent,
            data: {
              category: LETTERS_CATEGORY.OUTBOX,
              status: LETTER_OUTBOX_STATUS.ON_SIGN,
              lastPath: 'outbox/on-sign',
              isOutbox: true,
            },
          },
          {
            path: 'sent',
            component: LettersListComponent,
            data: {
              category: LETTERS_CATEGORY.OUTBOX,
              status: LETTER_OUTBOX_STATUS.SENT,
              lastPath: 'outbox/sent',
              isOutbox: true,
            },
          },
          {
            path: '**',
            redirectTo: 'on-sign',
          },
        ],
      },
      {
        path: 'inbox',
        component: LettersListComponent,
        data: {
          category: LETTERS_CATEGORY.INBOX,
          lastPath: 'inbox',
        },
      },
      {
        path: 'drafts',
        component: LettersListComponent,
        data: {
          category: LETTERS_CATEGORY.DRAFTS,
          lastPath: 'drafts',
        },
      },
      {
        path: 'create',
        component: LettersCreateComponent,
      },
      {
        path: 'edit/:id',
        component: LettersCreateComponent,
      },
      {
        path: '**',
        // redirectTo: 'create',
        redirectTo: 'outbox',
      },
    ],
  },
  {
    path: '**',
    redirectTo: '',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class LettersRoutingModule {}
