import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { TranslateService } from '@ngx-translate/core';

import { LETTER_STATUS } from '@shared/models';
import { NavService } from '@shared/nav/nav.service';

import { CompanyService } from '@company/company.service';

import { ILetter } from '../../models';
import { LettersService } from '../../services';
import { of } from 'rxjs';

@Component({
  selector: 'j-letters-group',
  templateUrl: './letters-group.component.html',
  styleUrls: ['./letters-group.component.scss'],
})
export class LettersGroupComponent {
  @Input() title: string;
  @Input() list: ILetter[];

  @Input() highlighted: string;
  @Input() isDeletable: boolean = false;
  @Input() isSelectable: boolean = false;
  @Input() isSignable: boolean = false;

  @Output() detailsShown = new EventEmitter<ILetter>();
  @Output() scrolled = new EventEmitter();

  translatePrefix = 'LETTERS.LIST';
  urlPath = 'letters';

  modalPartialTitleFn = this.mapModalPartialTitle.bind(this);
  highlightedFn = this.shouldBeHighlighted.bind(this);
  highlightedTemporarilyFn = this.shouldBeHighlightedTemporarily.bind(this);

  constructor(
    private lettersService: LettersService,
    private router: Router,
    private route: ActivatedRoute,
    private navService: NavService,
    private companyService: CompanyService,
    private translateService: TranslateService
  ) {}

  onClick(letter: ILetter) {
    letter.read = true;
    
    if (letter.statusCode === LETTER_STATUS.DRAFT) {
      this.router.navigate(['edit', letter.id], {
        relativeTo: this.route.parent,
      });
    } else {
      this.detailsShown.emit(letter);
    }
  }

  onScroll() {
    this.scrolled.emit();
  }

  shouldBeHighlighted(item: ILetter) {
    return !item.read;
  }

  shouldBeHighlightedTemporarily(item: ILetter) {
    return this.highlighted === item.id;
  }

  mapModalPartialTitle(item: ILetter) {
    return ` ${item.letterNumber}:`;
  }

  onClear() {
    this.navService.retrieveLetterBadges();
  }

  onSign() {
    if (
      this.companyService.isSingleSignScheme() ||
      (this.companyService.isMultipleSignScheme() &&
        this.companyService.isPrimaryAuthority())
    ) {
      this.router.navigate(['sent'], { relativeTo: this.route.parent });
    }
  }

  mapTranslate(key: string) {
    return `${this.translatePrefix}.${key}`;
  }

  mapDateText(letter: ILetter) {
    const mapped = this.lettersService.mapLetterDate(letter);
    return typeof mapped === 'string'
      ? of(mapped)
      : this.translateService.stream(mapped.key, mapped.params);
  }
}
