import { Component, OnInit, ViewChild } from '@angular/core';

import { combineLatest } from 'rxjs';

import { NotifierService } from 'angular-notifier';
import { TranslateService } from '@ngx-translate/core';

import { FilesService } from '@shared/services';

import { CompanyService } from '@company/company.service';

import { AccountDropdownComponent } from '@accounts/components';
import { AccountsService } from '@accounts/services/accounts.service';
import { ACCOUNT_TYPE, IAccountDropdownOption } from '@accounts/account.model';

import { CertificatesService } from '@letters/services';
import { CERTIFICATE_TYPE, ICertificateType } from '@letters/models';
import { NgbModal, NgbModalConfig } from '@ng-bootstrap/ng-bootstrap';
import { LettersCertificatesModalComponent } from '../letters-certificates-modal/letters-certificates-modal.component';
import { convertDateToApiFormat } from '@utils/dates';

const TRANSLATE_PREFIX = 'LETTERS.CERTIFICATES';
const MODAL_CONFIG: Partial<NgbModalConfig> = {
  backdropClass: 'backdrop_light',
  windowClass: 'modal_light modal_dictionary',
  backdrop: 'static',
  centered: true,
};

@Component({
  selector: 'j-letters-certificates',
  templateUrl: './letters-certificates.component.html',
  styleUrls: ['./letters-certificates.component.scss'],
})
export class LettersCertificatesComponent implements OnInit {
  types;
  isLoading: boolean = true;
  accounts: IAccountDropdownOption[];

  params;
  companyName;

  @ViewChild('accountDropdown') accountDropdown: AccountDropdownComponent;

  constructor(
    private companyService: CompanyService,
    private certificatesService: CertificatesService,
    private accountsService: AccountsService,
    private filesService: FilesService,
    private notifierService: NotifierService,
    private translateService: TranslateService,
    private modalService: NgbModal
  ) {
    combineLatest([
      this.accountsService.getAccountsList(),
      this.certificatesService.getCertificateTypes(),
    ]).subscribe((values) => {
      const [accounts, types] = values;

      this.mapAccounts(accounts);
      this.types = types;

      this.isLoading = false;
    });
  }

  ngOnInit() {}

  mapTranslate(value: string) {
    return `${TRANSLATE_PREFIX}.${value}`;
  }

  async onTypeSelect(type: ICertificateType) {
    if (type.code === CERTIFICATE_TYPE.ACCOUNT_TURNOVER) {
      this.showSertificatesModal(type);
    } else {
      this.downloadCertificate(type, this.params);
    }
  }

  onAccountSelect(account: IAccountDropdownOption) {
    if (account.accountType === ACCOUNT_TYPE.ALL) {
      this.params = null;
    } else {
      this.params = {
        iban: account.iban,
      };
    }
  }

  private async showSertificatesModal(type: ICertificateType) {
    const modalRef = this.modalService.open(
      LettersCertificatesModalComponent,
      MODAL_CONFIG
    );

    try {
      const range = await modalRef.result;

      this.downloadCertificate(type, {
        ...this.params,
        dateFrom: convertDateToApiFormat(range.from),
        dateTo: convertDateToApiFormat(range.to),
      });
    } catch (error) {}
  }

  private async downloadCertificate(
    type: ICertificateType,
    params: any = null
  ) {
    try {
      const certificate = await this.certificatesService.getCertificate(
        type.code,
        params
      );

      this.filesService.downloadFile(
        certificate,
        `${type.description} - ${this.companyService.currentCompany.name}.pdf`
      );
    } catch (error) {
      this.mapError(error);
    }
  }

  private async mapError(errorPromise) {
    const error = await errorPromise;
    const errorMessage =
      error.description ||
      error.code ||
      this.translateService.instant(this.mapTranslate('ERROR'));

    this.notifierService.notify('error', errorMessage);
  }

  private mapAccounts(accounts) {
    const filtered = accounts.filter(
      (account) => account.accountType === ACCOUNT_TYPE.ACCOUNT
    );

    if (filtered.length > 1) {
      this.accounts = [
        ...filtered,
        {
          accountType: ACCOUNT_TYPE.ALL,
        },
      ];
    } else {
      this.accounts = filtered;
    }

    setTimeout(() => {
      this.accountDropdown.selectAccount(
        this.accounts[this.accounts.length - 1]
      );
    }, 0);
  }
}
