import {
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';

import { Subscription } from 'rxjs';

import { LETTER_STATUS } from '@shared/models';
import { NavService } from '@shared/nav/nav.service';

import { CompanyService } from '@company/company.service';

import { ILetter, ILettersSearch, IOnSignCounts } from '../../models';
import { LettersService } from '../../services';

const TRANSLATE_PREFIX = 'LETTERS.LIST';
const PAGE_SIZE = 100000;

@Component({
  selector: 'j-letters-on-sign',
  templateUrl: './letters-on-sign.component.html',
  styleUrls: ['./letters-on-sign.component.scss'],
})
export class LettersOnSignComponent implements OnDestroy {
  @Input() highlighted: string;

  @Output() retrieved = new EventEmitter<IOnSignCounts>();
  @Output() detailsShown = new EventEmitter<ILetter>();

  isLoading: boolean = true;

  onSignForMeList: ILetter[] = [];
  onSignForOthersList: ILetter[] = [];

  lettersSearch: ILettersSearch = {
    incoming: false,
    statusCode: [LETTER_STATUS.ON_SIGN],
    pageSize: PAGE_SIZE,
  };

  errorText: string = null;

  subscription = new Subscription();

  constructor(
    private lettersService: LettersService,
    private navService: NavService,
    private companyService: CompanyService
  ) {
    this.subscription.add(
      this.companyService.refresh$.subscribe((companyRefresh) => {
        const [user, company, isAuthorized] = companyRefresh;

        if (isAuthorized) {
          this.retrieveLetters();
        }
      })
    );
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  mapTranslate(key: string) {
    return `${TRANSLATE_PREFIX}.${key}`;
  }

  onDetailsShown(letter: ILetter) {
    this.detailsShown.emit(letter);
  }

  onRetry() {
    this.retrieveLetters();
  }

  async retrieveLetters() {
    this.errorText = null;
    this.navService.retrieveLetterBadges();

    try {
      await Promise.all([
        this.retrieveOnSignForMe(),
        this.retrieveOnSignForOthers(),
      ]);

      this.retrieved.emit({
        onSignForMe: this.onSignForMeList.length,
        onSignForOthers: this.onSignForOthersList.length,
      });
    } catch (error) {
      this.errorText = error.description || error;
    } finally {
      this.isLoading = false;
    }
  }

  async retrieveOnSignForMe() {
    const letterList = await this.lettersService.getList({
      ...this.lettersSearch,
      onSignForMe: true,
    });

    this.onSignForMeList = letterList.items;
  }

  async retrieveOnSignForOthers() {
    const letterList = await this.lettersService.getList({
      ...this.lettersSearch,
      onSignForOthers: true,
    });

    this.onSignForOthersList = letterList.items;
  }
}
