import { Component, OnInit } from '@angular/core';

import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

const TRANSLATE_PREFIX = 'LETTERS.CERTIFICATES.MODAL';

@Component({
  selector: 'j-letters-certificates-modal',
  templateUrl: './letters-certificates-modal.component.html',
  styleUrls: ['./letters-certificates-modal.component.scss'],
})
export class LettersCertificatesModalComponent implements OnInit {
  constructor(public modal: NgbActiveModal) {}

  onClose() {
    this.modal.dismiss();
  }

  onSubmit(result) {
    this.modal.close(result);
  }

  ngOnInit() {}

  mapTranslate(value: string) {
    return `${TRANSLATE_PREFIX}.${value}`;
  }
}
