import { Inject, Injectable, LOCALE_ID } from '@angular/core';
import { formatDate } from '@angular/common';
import { HttpParams, HttpResponse } from '@angular/common/http';

import { BehaviorSubject } from 'rxjs';

import dayjs from 'dayjs';

import { convertDateToApiFormat } from '@utils/dates';
import { ApiService } from '@api/api.service';
import { CompanyService } from '@company/company.service';

import { IBulkOperationResult, ISignature } from '@shared/models';
import { FilesService } from '@shared/services';

import {
  ILetter,
  ILetterCreateForm,
  ILetterTopic,
  LETTER_TOPIC_TYPE,
  TOPIC_BACKUPS,
  ILettersSearch,
  ILettersList,
} from '../models';

import { ListService } from '@shared/services/list.service';
import { TranslateService } from '@ngx-translate/core';

@Injectable({
  providedIn: 'root',
})
export class LettersService {
  private files: File[] = [];
  cashedTopics: ILetterTopic[];

  lastLettersListPath$ = new BehaviorSubject('outbox');

  constructor(
    private companyService: CompanyService,
    private filesService: FilesService,
    private apiService: ApiService,
    private listService: ListService,
    private translateService: TranslateService
  ) {
    this.retrieveTopics();
  }

  clearFiles() {
    this.files = [];
    this.filesService.uploading$.next([]);
  }

  onFileDrop(fileList) {
    const dropped = [];

    for (const droppedFile of fileList) {
      dropped.push(droppedFile);
    }

    if (dropped.length > 0) {
      this.files = [...dropped];
      this.filesService.uploading$.next(this.files);
    }
  }

  mapLetterDate(letter: ILetter) {
    const dateTime = letter.created;

    const isToday = dayjs().isSame(dateTime, 'day');
    const isYesterday = dayjs().subtract(1, 'date').isSame(dateTime, 'day');

    const time = dayjs(dateTime).format('HH:mm');

    if (isToday) {
      return {
        key: 'LETTERS.DATE.TODAY',
        params: {
          time,
        },
      };
    } else if (isYesterday) {
      return {
        key: 'LETTERS.DATE.YESTERDAY',
        params: {
          time,
        },
      };
    } else {
      const format = dayjs().isSame(dateTime, 'year')
        ? 'd MMMM HH:mm'
        : 'd MMMM yyyy HH:mm';
      return formatDate(dateTime, format, this.translateService.currentLang);
    }
  }

  getById(id: string) {
    const url = this.mapUrl(`/${id}`);
    return this.apiService.get<ILetter>(url);
  }

  getLetterSignatures(id: string) {
    const url = this.mapUrl(`/${id}/signatures`);
    return this.apiService.get<ISignature[]>(url);
  }

  sign(id: string) {
    return this.listService.sign('letters', id);
  }

  async getList(searchParams: ILettersSearch = {}): Promise<ILettersList> {
    const url = this.mapUrl();

    let params = new HttpParams();

    for (const key in searchParams) {
      if (Object.prototype.hasOwnProperty.call(searchParams, key)) {
        let value = searchParams[key];

        if (value) {
          if (key == 'dateFrom' || key == 'dateTo') {
            value = convertDateToApiFormat(value);
          }

          params = params.append(key, value);
        }
      }
    }

    const extras = {
      observe: 'response',
    };

    const response = await this.apiService.get<HttpResponse<ILetter[]>>(
      url,
      null,
      params,
      extras
    );

    return {
      items: response.body,
      totalCount: +response.headers.get('X-Total-Count'),
    };
  }

  async retrieveTopics() {
    const url = this.mapUrl(`/topics`);
    let topics;

    try {
      topics = await this.apiService.get<ILetterTopic[]>(url);
    } catch (error) {
      topics = TOPIC_BACKUPS;
    }

    this.cashedTopics = topics;
    return this.cashedTopics;
  }

  async getTopics(topicType: LETTER_TOPIC_TYPE) {
    if (this.cashedTopics) {
      return this.filterTopicsByType(topicType);
    }

    this.cashedTopics = await this.retrieveTopics();
    return this.filterTopicsByType(topicType);
  }

  async getTopicValues() {
    if (this.cashedTopics) {
      return this.mapTopicValues();
    }

    this.cashedTopics = await this.retrieveTopics();
    return this.mapTopicValues();
  }

  edit(body: ILetter) {
    const url = this.mapUrl(`/${body.id}`);
    return this.apiService.put<ILetter>(url, body);
  }

  create(body: ILetterCreateForm) {
    const url = this.mapUrl();
    return this.apiService.post<ILetter>(url, body);
  }

  sendToSign(itemIds: string | string[]) {
    const isBulk = Array.isArray(itemIds);

    const url = this.mapUrl(isBulk ? ':on-sign' : `/${itemIds}:on-sign`);
    const body = isBulk
      ? {
          itemIds,
        }
      : null;

    return this.apiService.patch<IBulkOperationResult>(url, body);
  }

  private filterTopicsByType(topicType: LETTER_TOPIC_TYPE) {
    return this.cashedTopics.filter((topic) => topic.type === topicType);
  }

  private mapTopicValues() {
    return this.cashedTopics.map((topic) => topic.displayName);
  }

  private mapUrl(path: string = '') {
    return `companies/${this.companyService.currentId}/letters${path}`;
  }
}
