import {
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
} from '@angular/core';

import { Subscription } from 'rxjs';

import { NavService } from '@shared/nav/nav.service';

import { CompanyService } from '@company/company.service';

import { FilterService } from '@filter/filter.service';
import { FILTER_PERIOD_TYPE } from '@filter/filter.model';

import { LettersService } from '../../services';
import { ILetter, ILettersSearch } from '../../models';

const TRANSLATE_PREFIX = 'LETTERS.LIST';
const PAGE_START = 1;
const PAGE_SIZE = 10;

@Component({
  selector: 'j-letters-list-base',
  templateUrl: './letters-list-base.component.html',
  styleUrls: ['./letters-list-base.component.scss'],
})
export class LettersListBaseComponent implements OnDestroy, OnInit {
  isLoading: boolean = true;
  hasMorePages: boolean = true;
  list: ILetter[] = [];

  @Input() emptyNotFound: string = 'LETTERS.EMPTY.NOT_FOUND';
  @Input() emptyLabel: string = 'LETTERS.EMPTY.DEFAULT';
  @Input() highlighted: string;
  @Input() listParams: Partial<ILettersSearch>;
  @Input() isSelectable: boolean = false;
  @Input() isSignable: boolean = false;
  @Input() isDeletable: boolean = false;
  @Input() hasDateFilter: boolean = false;

  @Output() retrieved = new EventEmitter<number>();
  @Output() detailsShown = new EventEmitter<ILetter>();

  page = PAGE_START;
  pageSize = PAGE_SIZE;

  currentDates = null;
  errorText: string = null;

  subscription = new Subscription();

  constructor(
    private lettersService: LettersService,
    private companyService: CompanyService,
    private navService: NavService,
    private filterService: FilterService
  ) {}

  ngOnInit() {
    this.filterService.updatePeriod(FILTER_PERIOD_TYPE.NONE);
    this.subscription.add(
      this.companyService.refresh$.subscribe((companyRefresh) => {
        const [user, company, isAuthorized] = companyRefresh;

        if (isAuthorized) {
          this.onClearPeriod();
        }
      })
    );
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  onRetry() {
    this.retrieveLetters({ page: PAGE_START });
  }

  onFilter(dates: { to: Date; from: Date }) {
    this.currentDates = dates;
    this.retrieveLetters({
      page: PAGE_START,
      dateFrom: dates.from,
      dateTo: dates.to,
    });
  }

  onClearPeriod() {
    this.currentDates = null;
    this.filterService.updatePeriod(FILTER_PERIOD_TYPE.NONE);
    this.retrieveLetters({ page: PAGE_START });
  }

  onScroll() {
    if (this.isLoading || !this.hasMorePages) {
      return;
    }

    this.retrieveLetters({ page: ++this.page });
  }

  onDetailsShown(letter: ILetter) {
    this.detailsShown.emit(letter);
  }

  mapTranslate(key: string) {
    return `${TRANSLATE_PREFIX}.${key}`;
  }

  async retrieveLetters(search: ILettersSearch) {
    this.isLoading = true;
    this.errorText = null;

    try {
      let filter = {
        ...this.listParams,
        ...search,
        pageSize: this.pageSize,
      };

      const letterList = await this.lettersService.getList(filter);

      this.hasMorePages = letterList.totalCount > search.page * this.pageSize;
      this.page = search.page;
      this.list =
        search.page > PAGE_START
          ? [...this.list, ...letterList.items]
          : letterList.items;

      this.retrieved.emit(this.list.length);
      this.navService.retrieveLetterBadges();
    } catch (error) {
      this.errorText = error.description || error;
    } finally {
      this.isLoading = false;
    }
  }
}
