import { Component, EventEmitter, OnDestroy, Output } from '@angular/core';

import { Subscription } from 'rxjs';

import { LETTER_TOPIC_TYPE } from '@letters/models';
import { CertificatesService } from '@letters/services';

const TRANSLATE_PREFIX = 'LETTERS.CREATE_CONTENT';

@Component({
  selector: 'j-letters-create-content',
  templateUrl: './letters-create-content.component.html',
  styleUrls: ['./letters-create-content.component.scss'],
})
export class LettersCreateContentComponent implements OnDestroy {
  @Output() closed = new EventEmitter();

  LETTER_TOPIC_TYPE = LETTER_TOPIC_TYPE;

  subscription = new Subscription();
  isCertificatesShown = false;

  constructor(private certificatesService: CertificatesService) {
    const certificatesSubscription = this.certificatesService
      .getCertificateTypes()
      .subscribe((certificates) => {
        this.isCertificatesShown = certificates?.length > 0;
      });

    this.subscription.add(certificatesSubscription);
  }
  
  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  mapTranslate(value: string) {
    return `${TRANSLATE_PREFIX}.${value}`;
  }

  onClose() {
    this.closed.emit();
  }
}
