import { Injectable } from '@angular/core';

import { BehaviorSubject, Observable, Subject } from 'rxjs';

import { ApiService } from '@api/api.service';
import { CompanyService } from '@company/company.service';

import { CERTIFICATE_TYPE, ICertificateType } from '@letters/models';

@Injectable({
  providedIn: 'root',
})
export class CertificatesService {
  private cashedCertificateTypes$ = new BehaviorSubject<ICertificateType[]>([]);

  constructor(
    private apiService: ApiService,
    private companyService: CompanyService
  ) {
    this.retrieveCertificateTypes();
  }

  async getCertificate(certificateType: CERTIFICATE_TYPE, params = null) {
    try {
      const url = this.mapUrl();
      const body = {
        certificateTypeCode: certificateType,
        params,
      };

      return await this.apiService.post(url, body, null, null, {
        responseType: 'blob',
      });
    } catch (error) {
      const reader: FileReader = new FileReader();

      const result = new Promise((resolve) => {
        reader.onloadend = () => {
          resolve(JSON.parse(reader.result as string));
        };
      });

      reader.readAsText(error.error);

      throw result;
    }
  }

  getCertificateTypes() {
    return this.cashedCertificateTypes$.asObservable();
  }

  private async retrieveCertificateTypes() {
    const url = this.mapUrl(`/types`);

    let types;

    try {
      types = await this.apiService.get<ICertificateType[]>(url);
    } catch (error) {
      types = [];
    }

    this.cashedCertificateTypes$.next(types);
    return types;
  }

  private mapUrl(path: string = '') {
    return `companies/${this.companyService.currentId}/certificates${path}`;
  }
}
