import {
  AUTHORITY_TYPE,
  IFile,
  ISignature,
  LETTER_STATUS,
  SIGNATURE_SCHEME,
} from '@shared/models';

export enum LETTERS_CATEGORY {
  INBOX = 'inbox',
  OUTBOX = 'outbox',
  DRAFTS = 'drafts',
}

export enum LETTER_SIGNATURE_TYPE {
  LETTER = 'LETTER',
  PAYMENT = 'PAYMENT',
}

export interface ILetterCreateForm {
  id: string;
  title: string;
  body: string;
  fileIds: string[];

  statusCode?: LETTER_STATUS;
}

export interface ILetter extends ILetterCreateForm {
  colvirId: string;
  companyId: string;

  created: Date;
  letterNumber: number;
  read: boolean;
  incoming: boolean;

  files: IFile[];

  onSignAuthority: AUTHORITY_TYPE;
  signatureScheme: SIGNATURE_SCHEME;
  signatures: ISignature[];

  statusCode: LETTER_STATUS;
}

export enum LETTER_OUTBOX_STATUS {
  ON_SIGN = 'ON_SIGN',
  SENT = 'SENT',
}

export interface IOnSignCounts {
  onSignForMe: number;
  onSignForOthers: number;
}

export interface ILettersList {
  totalCount: number;
  items: ILetter[];
}
