import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ILetterTopic, LETTER_TOPIC_TYPE } from '@letters/models';
import { LettersService } from '@letters/services';

const TRANSLATE_PREFIX = 'LETTERS.CREATE_CONTENT';

@Component({
  selector: 'j-letters-create-group',
  templateUrl: './letters-create-group.component.html',
  styleUrls: ['./letters-create-group.component.scss'],
})
export class LettersCreateGroupComponent implements OnInit {
  @Input() topicType: LETTER_TOPIC_TYPE;

  topics: ILetterTopic[];
  isLoading: boolean = true;

  constructor(private lettersService: LettersService, private router: Router) {}

  ngOnInit() {
    this.loadTopics();
  }

  mapTranslate(value: string) {
    return `${TRANSLATE_PREFIX}.${value}`;
  }

  onLinkSelect(topic: ILetterTopic) {
    this.router.navigate(['/letters/create'], {
      state: {
        title: topic.displayName,
      },
    });
  }

  private async loadTopics() {
    try {
      this.topics = await this.lettersService.getTopics(this.topicType);
    } catch (error) {
    } finally {
      this.isLoading = false;
    }
  }
}
