import { Component, OnDestroy, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';


import { combineLatest, Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

import { LETTER_STATUS } from '@shared/models';
import { NavService } from '@shared/nav/nav.service';

import { LettersService } from '../../services';
import { ILetter, ILettersSearch, LETTERS_CATEGORY } from '../../models';
import { LettersAsideComponent } from '../letters-aside/letters-aside.component';

@Component({
  selector: 'j-letters-list',
  templateUrl: './letters-list.component.html',
  styleUrls: ['./letters-list.component.scss'],
})
export class LettersListComponent implements OnDestroy {
  lettersCategory: LETTERS_CATEGORY;
  LETTERS_CATEGORY = LETTERS_CATEGORY;

  subscription = new Subscription();

  inboxParams: Partial<ILettersSearch> = {
    incoming: true,
  };

  draftsParams: Partial<ILettersSearch> = {
    statusCode: [LETTER_STATUS.DRAFT],
  };

  highlighted: string;

  @ViewChild('lettersAside', { static: false })
  lettersAside: LettersAsideComponent;

  constructor(
    private route: ActivatedRoute,
    private navService: NavService,
    private lettersService: LettersService
  ) {
    const routeSubscription = combineLatest([
      this.route.paramMap.pipe(map(() => window.history.state)),
      this.route.data,
    ]).subscribe((values) => {
      const [state, data] = values;

      this.lettersCategory = data.category;
      this.lettersService.lastLettersListPath$.next(data.lastPath);

      if (state.highlighted) {
        this.highlighted = state.highlighted;
      }
    });

    this.subscription.add(routeSubscription);
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  onDetailsShown(letter: ILetter) {
    this.lettersAside.showDetails(letter);
  }

  onCreateShown() {
    this.lettersAside.showCreate();
  }

  onAsideClose() {
    this.navService.retrieveLetterBadges();
  }
}
