import { Component, EventEmitter, OnDestroy, Output } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { Subscription } from 'rxjs';

import { NavService } from '@shared/nav/nav.service';

@Component({
  selector: 'j-letters-nav',
  templateUrl: './letters-nav.component.html',
  styleUrls: ['./letters-nav.component.scss'],
})
export class LettersNavComponent implements OnDestroy {
  isOutboxRoute: boolean;
  letterTypes = [
    {
      id: 'outbox',
      label: 'LETTERS.TYPES.OUTBOX',
      url: '/letters/outbox',
      badge: null,
      badgeClass: 'badge-primary',
    },
    {
      id: 'inbox',
      label: 'LETTERS.TYPES.INBOX',
      url: '/letters/inbox',
      badge: null,
      badgeClass: 'badge-primary',
    },
    {
      id: 'draft',
      label: 'LETTERS.TYPES.DRAFT',
      url: '/letters/drafts',
      badge: null,
      badgeClass: 'badge-light',
    },
  ];

  @Output() createShown = new EventEmitter();

  subscription = new Subscription();

  constructor(
    private navService: NavService,
    private route: ActivatedRoute
  ) {
    this.route.data.subscribe((data) => {
      this.isOutboxRoute = data.isOutbox || false;
    });

    this.subscription.add(
      this.navService.getLetterBadges().subscribe((badges) => {
        this.setBadge('outbox', badges.onSignForMe);
        this.setBadge('inbox', badges.unread);
        this.setBadge('draft', badges.draft);
      })
    );
  }

  isBadgesShown(id: string) {
    switch (id) {
      case 'inbox':
      case 'draft':
        return true;
      case 'outbox':
        return !this.isOutboxRoute;
    }
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  private setBadge(type: string, count: number) {
    this.letterTypes.find((item) => item.id === type).badge = count;
  }

  onCreateShown() {
    this.createShown.emit();
  }
}
