import { Component, OnDestroy } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { FormControl, FormGroup, Validators } from '@angular/forms';

import { BehaviorSubject, combineLatest, Subscription } from 'rxjs';
import { map } from 'rxjs/operators';

import { NotifierService } from 'angular-notifier';
import { TranslateService } from '@ngx-translate/core';

import { CompanyService } from '@company/company.service';

import { IUploadingFile, LETTER_STATUS } from '@shared/models';
import { FilesService } from '@shared/services';

import { ILetter, ILetterCreateForm } from '../../models';
import { LettersService } from '../../services';

const TRANSLATE_PREFIX = 'LETTERS.CREATE';
const BODY_MAX_LENGTH = 10000;

enum NOTIFICATIONS {
  SAVED_TO_DRAFT = 'NOTIFICATIONS.SAVED_TO_DRAFT',
  SENT_FOR_SIGN = 'NOTIFICATIONS.SENT_FOR_SIGN',
  SAVED_EDITING = 'NOTIFICATIONS.SAVED_EDITING',
  SIGNED = 'NOTIFICATIONS.SIGNED',
  SAVED_TO_DRAFT_ERROR = 'NOTIFICATIONS.SAVED_TO_DRAFT_ERROR',
  SENT_FOR_SIGN_ERROR = 'NOTIFICATIONS.SENT_FOR_SIGN_ERROR',
  SAVED_EDITING_ERROR = 'NOTIFICATIONS.SAVED_EDITING_ERROR',
  SIGNED_ERROR = 'NOTIFICATIONS.SIGNED_ERROR',
}

@Component({
  selector: 'j-letters-create-form',
  templateUrl: './letters-create-form.component.html',
  styleUrls: ['./letters-create-form.component.scss'],
})
export class LettersCreateFormComponent implements OnDestroy {
  acceptedFormats = '.doc, .docx, .pdf, .jpg, .jpeg, .png, .xls, .xlsx';
  maxSize = 5000000;

  topics: string[];
  form: FormGroup;

  isSubmitted = false;

  isLoading = false;
  isSubmitting = false;

  isEditing = false;

  uploadingFiles: IUploadingFile[] = [];

  bodyMaxLength = BODY_MAX_LENGTH;
  bodyLengthMessage$ = new BehaviorSubject(`0/${this.bodyMaxLength}`);

  subscription = new Subscription();
  originalLetter: ILetter;

  createAndSignLabel: string;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private lettersService: LettersService,
    private companyService: CompanyService,
    private filesService: FilesService,
    private notifierService: NotifierService,
    private translateService: TranslateService
  ) {
    const routeSubscription = combineLatest([
      this.route.paramMap.pipe(map(() => window.history.state)),
      this.route.params,
    ]).subscribe((values) => {
      const [state, params] = values;

      if (state.letter) {
        this.isEditing = true;
        this.loadEditLetterByBody(state.letter, state.topics, state.files);
      } else if (params.id) {
        this.isEditing = true;
        this.loadEditLetterById(params.id);
      } else {
        this.loadNewLetter(state.title);
      }
    });

    this.subscription.add(routeSubscription);

    this.createAndSignLabel = this.companyService.isSecondaryAuthority()
      ? 'SIGN'
      : 'CREATE_AND_SIGN';
  }

  ngOnDestroy() {
    this.lettersService.clearFiles();
    this.subscription.unsubscribe();
  }

  get f() {
    return this.form.controls;
  }

  get canSendAndSign() {
    return (
      this.companyService.isSecondaryAuthority() ||
      (this.companyService.isPrimaryAuthority() &&
        this.companyService.isSingleSignScheme())
    );
  }

  get canSendOnly() {
    return (
      this.companyService.isNoSignAuthority() ||
      (this.companyService.isPrimaryAuthority() &&
        this.companyService.isMultipleSignScheme())
    );
  }

  loadEditLetter(letter: ILetter) {
    this.originalLetter = letter;

    setTimeout(() => {
      const { id, title, body } = letter;

      this.form.setValue({
        id,
        title,
        body,
        uploadModel: null,
      });

      if (letter.files?.length > 0) {
        this.uploadingFiles = letter.files.map((file) => {
          return {
            isLoading: false,
            file: file,
          };
        });
      }
    }, 0);
  }

  async loadEditLetterByBody(
    letter: ILetter,
    topics: string[],
    files: IUploadingFile[] = []
  ) {
    this.uploadingFiles = files;
    this.topics = topics;
    this.loadEditLetter(letter);
    this.initForm(letter);
  }

  async loadEditLetterById(letterId: string) {
    this.isLoading = true;

    try {
      const letter = await this.lettersService.getById(letterId);
      await this.loadTopics();

      this.loadEditLetter(letter);
      this.initForm(letter);
    } catch (error) {
    } finally {
      this.isLoading = false;
    }
  }

  async loadNewLetter(title: string = '') {
    this.isLoading = true;

    try {
      await this.loadTopics();
      this.initForm({ title });
    } catch (error) {
    } finally {
      this.isLoading = false;
    }
  }

  async loadTopics() {
    this.topics = await this.lettersService.getTopicValues();
  }

  initForm(initialValues: Partial<ILetterCreateForm> = null) {
    const { id, title, body } = {
      id: initialValues.id || null,
      title: initialValues.title || '',
      body: initialValues.body || '',
    };

    this.form = new FormGroup({
      id: new FormControl(id),
      title: new FormControl(title, Validators.required),
      body: new FormControl(body, [
        Validators.required,
        Validators.maxLength(this.bodyMaxLength),
      ]),
      uploadModel: new FormControl(''),
    });

    const formSubscription = this.form.valueChanges.subscribe((value) => {
      this.isSubmitted = false;
    });

    const bodySubscription = this.f.body.valueChanges.subscribe((body) => {
      this.bodyLengthMessage$.next(`${body.length}/${this.bodyMaxLength}`);
    });

    this.subscription.add(bodySubscription);
    this.subscription.add(formSubscription);
  }

  prepareCreate() {
    this.isSubmitted = true;

    if (this.form.invalid) {
      return false;
    }

    if (this.filesService.hasLoadingFiles(this.uploadingFiles)) {
      this.notifierService.notify(
        'default',
        this.translateService.instant('FILES.PLEASE_WAIT')
      );
      return false;
    }

    const hasErroredFiles = this.filesService.hasErroredFiles(
      this.uploadingFiles
    );

    if (hasErroredFiles) {
      this.notifierService.notify(
        'default',
        this.translateService.instant('FILES.HAS_ERRORED_FILES')
      );
    }

    return true;
  }

  async saveToDraft() {
    if (this.isSubmitting) {
      return;
    }

    this.isSubmitting = true;

    try {
      const result = await this.createDraft();

      this.showNotification(NOTIFICATIONS.SAVED_TO_DRAFT);

      this.router.navigate(['/letters/drafts'], {
        state: {
          highlighted: result.id,
        },
      });
      this.isSubmitting = false;
    } catch (error) {
      this.showNotification(NOTIFICATIONS.SAVED_TO_DRAFT_ERROR, 'error');
      this.isSubmitting = false;
    }
  }

  async createAndSendForSign() {
    if (this.isSubmitting) {
      return;
    }

    const canBeCreated = this.prepareCreate();

    if (!canBeCreated) {
      return;
    }

    this.isSubmitting = true;

    try {
      const result = await this.lettersService.create({
        ...this.mapLetterBody(),
        statusCode: LETTER_STATUS.ON_SIGN,
      });

      this.showNotification(NOTIFICATIONS.SENT_FOR_SIGN);

      this.router.navigate(['/letters/outbox/on-sign'], {
        state: {
          highlighted: result.id,
        },
      });
    } catch (error) {
      this.showNotification(NOTIFICATIONS.SENT_FOR_SIGN_ERROR, 'error');
      this.isSubmitting = false;
    }
  }

  async createDraftAndNavigate() {
    try {
      this.originalLetter = await this.createDraft();

      this.router.navigate(['edit', this.originalLetter.id], {
        relativeTo: this.route.parent,
        state: {
          letter: this.originalLetter,
          topics: this.topics,
          files: this.uploadingFiles,
        },
      });
    } catch (error) {}
  }

  async createAndSign() {
    if (this.isSubmitting) {
      return;
    }

    const canBeCreated = this.prepareCreate();

    if (!canBeCreated) {
      return;
    }

    this.isSubmitting = true;

    try {
      await this.createDraftAndNavigate();
      await this.lettersService.sign(this.originalLetter.id);

      this.showNotification(NOTIFICATIONS.SIGNED);

      if (this.companyService.isSecondaryAuthority()) {
        this.router.navigate(['/letters/outbox/on-sign'], {
          state: {
            highlighted: this.originalLetter.id,
          },
        });
      } else {
        this.router.navigate(['/letters/outbox/sent'], {
          state: {
            highlighted: this.originalLetter.id,
          },
        });
      }
    } catch (error) {
      this.showNotification(NOTIFICATIONS.SIGNED_ERROR, 'error');
      this.isSubmitting = false;
    }
  }

  async edit() {
    this.isSubmitting = true;

    try {
      await this.lettersService.edit({
        ...this.originalLetter,
        ...this.mapLetterBody(),
      });

      this.showNotification(NOTIFICATIONS.SAVED_EDITING);

      this.router.navigate(['/letters/drafts'], {
        state: {
          highlighted: this.originalLetter.id,
        },
      });
    } catch (error) {
      this.showNotification(NOTIFICATIONS.SAVED_EDITING_ERROR, 'error');
      this.isSubmitting = false;
    }
  }

  createDraft() {
    return this.lettersService.create({
      ...this.mapLetterBody(),
      statusCode: LETTER_STATUS.DRAFT,
    });
  }

  mapTranslate(key: string) {
    return `${TRANSLATE_PREFIX}.${key}`;
  }

  mapBodyErrors() {
    if (!this.f.body.errors) {
      return null;
    }

    if (this.f.body.errors.maxlength) {
      return 'ERRORS.MAX_LENGTH';
    }
  }

  onTitleSelect(title) {
    this.f.title.setValue(title);
  }

  onAddFile(event) {
    if (!event?.target?.files) {
      return;
    }

    this.lettersService.onFileDrop(event.target.files);
    this.f.uploadModel.setValue(null);
  }

  onUploadingFilesUpdate(uploadingFiles: IUploadingFile[]) {
    this.uploadingFiles = uploadingFiles;
  }

  private showNotification(key: string, type: string = 'default') {
    const translateKey = this.mapTranslate(key);
    const message = this.translateService.instant(translateKey);

    this.notifierService.notify(type, message);
  }

  private mapLetterBody() {
    return {
      ...this.form.getRawValue(),
      fileIds: this.uploadingFiles
        .filter((item) => {
          return !item.error && item.file?.id;
        })
        .map((item) => {
          return item.file.id;
        }),
    };
  }
}
