import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { CompanyService } from '@company/company.service';

import { IFile, ISignature } from '@shared/models';
import { FilesService } from '@shared/services';

import { ILetter } from '../../models';
import { LettersService } from '../../services';

const TRANSLATE_PREFIX = 'LETTERS.DETAILS';

@Component({
  selector: 'j-letters-details-content',
  templateUrl: './letters-details-content.component.html',
  styleUrls: ['./letters-details-content.component.scss'],
})
export class LettersDetailsContentComponent implements OnInit {
  @Input() letter: ILetter;
  @Output() closed = new EventEmitter();

  signers;
  isFilesLoading = true;

  dateText: string;
  date: {
    key: string;
    params: {
      time: string;
    };
  };

  constructor(
    private router: Router,
    private lettersService: LettersService,
    private filesService: FilesService,
    private companyService: CompanyService
  ) {}

  ngOnInit() {
    this.mapDateText();
    this.loadAttachments();
  }

  onClose() {
    this.closed.emit();
  }

  onFileClick(file: IFile) {
    this.filesService.downloadFileById(file.id, file.name);
  }

  onReply() {
    this.router.navigate(['/letters/create'], {
      state: {
        title: this.mapTranslate('TITLE'),
      },
    });
  }

  mapTranslate(key: string) {
    return `${TRANSLATE_PREFIX}.${key}`;
  }

  private async loadAttachments() {
    this.isFilesLoading = true;

    try {
      const letter = await this.lettersService.getById(this.letter.id);
      this.letter = { ...this.letter, ...letter };

      this.loadSignatures();
    } catch (error) {
    } finally {
      this.isFilesLoading = false;
    }
  }

  private async loadSignatures() {
    if (this.letter.incoming) {
      return;
    }

    this.signers = this.companyService.generateSignersList(
      this.letter.statusCode,
      this.letter.signatureScheme,
      this.letter.signatures
    );
  }

  private mapDateText() {
    const mapped = this.lettersService.mapLetterDate(this.letter);

    if (typeof mapped === 'string') {
      this.dateText = mapped;
    } else {
      this.date = mapped;
    }
  }
}
